@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Noto+Sans:400,400i,700,700i&display=swap&subset=korean');
/* font-family: 'Noto Sans', sans-serif;
font-family: 'Noto Sans KR', sans-serif; */
* {
  box-sizing: border-box;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 18px;
  color: #111;
  font: inherit;
  vertical-align: baseline;
  font-family: 'Noto Sans', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
body {
  background: #333;
}
#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.todo__title {
  color: #fefefe;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 0.05rem;
  margin-top: 20px;
  margin-left: 20px;
  text-align: left;
}
.todo__title span {
  color: #fefefe;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.05rem;
  margin-left: 10px;
}
.typeArea__wrapper {
  display: flex;
  align-items: center;
  margin: 30px auto 0;
  width: 90%;
}
.typeArea__create {
  background: none;
  border: none;
  border-bottom: 1px solid #fefefe;
  border-radius: 0;
  color: #fefefe;
  font-size: 17px;
  outline-style: none;
  letter-spacing: 0.02rem;
  line-height: 2rem;
  width: 100%;
}
.typeArea__create::placeholder {
  color: #fefefe;
  font-weight: 100;
  font-size: 1rem;
  letter-spacing: 0.05rem;
  opacity: 0.4;
}
.typeArea__addBtn {
  background: none;
  border: 1px solid #fefefe;
  border-radius: 5px;
  box-shadow: none;
  color: #fefefe;
  font-weight: bold;
  font-size: 1rem;
  letter-spacing: 0.03rem;
  margin-left: 10px;
  outline-style: none;
  padding: 7px 10px;
  transition: 0.35s linear;
}
.typeArea__editBtn {
  background: none;
  border: 1px solid #fefefe;
  border-radius: 5px;
  color: #fefefe;
  font-weight: bold;
  font-size: 1rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  outline-style: none;
  padding: 7px 20px;
}
.typeArea__cancelBtn {
  background: none;
  border: none;
  border-radius: 5px;
  color: #fefefe;
  font-weight: bold;
  font-size: 1rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  outline-style: none;
  position: relative;
  width: 18px;
  height: 18px;
}
.typeArea__cancelBtn span {
  background: #fefefe;
  border-radius: 2px;
  height: 3px;
  left: 0;
  position: absolute;
  width: 100%;
}
.typeArea__cancelBtn span:nth-of-type(1) {
  transform: rotate(45deg);
}
.typeArea__cancelBtn span:nth-of-type(2) {
  transform: rotate(-45deg);
}
.list__wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 30px auto 10px;
}
.list__wrapper li {
  margin-top: 10px;
  width: 90%;
}
.list__checkBox {
  display: none;
}
.list__checkBox + label {
  background: none;
  border: 1px solid #fefefe;
  border-radius: 20%;
  outline-style: none;
  height: 20px;
  margin-left: 0;
  width: 20px;
}
.list__checkBox:checked + label {
  background: url('./images/correct.png') no-repeat center / cover;
  border: none;
  border-radius: 20%;
  outline-style: none;
  height: 20px;
  margin-left: 0;
  width: 20px;
}
.list__checkBox:checked + label + .list__thing {
  text-decoration: line-through;
  opacity: 0.5;
}
.list__editwrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.list__thing {
  background: none;
  border: none;
  outline-style: none;
  color: #fefefe;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 100;
  font-size: 1rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  width: 60%;
}
.list__edit {
  background: none;
  border: 1px solid #fefefe;
  border-radius: 5px;
  color: #fefefe;
  font-weight: bold;
  font-size: 0.6rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  outline-style: none;
  padding: 7px 0;
  width: 10%;
}
.list__empty {
  background: none;
  border: none;
  border-radius: 5px;
  color: #fefefe;
  font-weight: bold;
  font-size: 0.6rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  outline-style: none;
  padding: 7px 0;
  width: 10%;
}
.list__remove {
  background: none;
  border: 1px solid #fefefe;
  border-radius: 5px;
  color: #fefefe;
  font-weight: bold;
  font-size: 0.6rem;
  letter-spacing: 0.05rem;
  margin-left: 10px;
  outline-style: none;
  padding: 7px 0;
  width: 10%;
}
